import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { VehicleModel } from 'src/app/shared/models/model';
import { ToastrService } from 'src/app/shared/services/toastr-message.service';
import { UserService } from 'src/app/shared/services/user.service';
import { VehicleService } from 'src/app/shared/services/vehicle.service';

@Component({
  selector: 'app-vehicle',
  templateUrl: './vehicle.component.html'
})
export class VehicleComponent implements OnInit {
  display: boolean = false;
  vehicleForm!: FormGroup;
  vehicles: any[] = [];


  constructor(private fb: FormBuilder, private vehicleService: VehicleService, private userService: UserService, private toastrService: ToastrService) {
    this.vehicleForm = this.fb.group({
      id: [''],
      institutionId: ['', Validators.required],
      number: ['', Validators.required],
      identificationLable: ['', Validators.required],
      identificationValue: ['', Validators.required],
    });
  }

  ngOnInit() {
    this.getVehicle();
  }

  getVehicle() {
    this.vehicleService.get(this.userService.selectedInstitutionId).subscribe((response: any) => {
      this.vehicles = response;
    });
  }
  
  updateData() {
    let temp = [...this.vehicles];
    temp = JSON.parse(JSON.stringify(temp));
    this.vehicles = temp;
  }

  add() {
    const data = this.vehicleForm.value;
    if (!data.id) {
      delete data.id;
    }

    this.vehicleService.create(data).subscribe({
      next: (response: any) => {
      if (!!response.id) {
        this.vehicles.push(response);
        this.updateData();
          this.toastrService.success('Vechiles Create Successfully.');
        this.display = false;
      }
    },
    error: () => {
      this.toastrService.error('Vechiles Create Failed.');
    }
    });
  }

  edit() {
    this.vehicleService.update(this.vehicleForm.value).subscribe((response: any) => {
      const data = this.vehicleForm.value;
      const index = this.vehicles.findIndex(i => i.id === data.id);
      this.vehicles[index] = data;
      this.toastrService.success('Vechiles Updated Successfully.');
      this.display = false;
    });
  }

  showDialog() {
    this.display = true;
    this.vehicleForm.reset();
    this.vehicleForm.get('institutionId')?.setValue(this.userService.selectedInstitutionId);
  }
  onSubmit() {
    if (this.vehicleForm.invalid) {
      return;
    }

    if (!this.vehicleForm.value.id) {
      this.add();
    } else {
      this.edit();
    }
  }

  fillForm(vechile: VehicleModel) {
    this.display = true;
    this.vehicleForm.patchValue(vechile);
  }

  deleteVechile(id: number) {
    this.vehicleService.delete(id).subscribe((response: any) => {
      const index = this.vehicles.findIndex(i => i.id == id);
      this.vehicles.splice(index, 1);
      this.toastrService.success('Vechiles Deleted Successfully.');
    });
  }

  public close(): void {
    window.alert('Are u Sure Leave From');
    this.display = false;
  }

}
